                      <div v-if="compareData.selectedData.budgetReach.budget.format.IP">{{ `In-Page (${compareData.selectedData.budgetReach.budget.format.IP}%)` }}</div>
                      <div v-if="compareData.selectedData.budgetReach.budget.format.IR">{{ `In-Read (${compareData.selectedData.budgetReach.budget.format.IR}%)` }}</div>
                    </template>
                    <template v-if="compareData.selectedData.budgetReach.type === 'reach'">
                      {{ `Mobile In-Read、${compareData.selectedData.budgetReach.reach.format === 'IR' ? 'In-Read' : 'In-Page'}` }}
