                  <div class="head">年齡性別</div>
                  <div class="detail">{{ showDemographic(compareData.selectedData.demographics) }}</div>
                </div>
              </div>
              <!-- 第二列 -->
              <div class="compare-table-row">
                <div class="compare-table-column">
                  <div class="head">時段選擇</div>
                  <div class="detail">{{ showOptionText('hourlies', compareData.selectedData.hourlies) }}</div>
                </div>
                <div class="compare-table-column">
                  <div class="head">興趣與特定議題選擇</div>
                  <div class="detail">
                    <div>- 興趣 -</div>
                    <div>{{ showInterest(compareData.selectedData.interestKeyword)}}</div>
                    <div>- 特定議題 -</div>
                    <div>{{ showKeyword(compareData.selectedData.interestKeyword)}}</div>
                  </div>
                </div>
                <div class="compare-table-column">
                  <div class="head">媒體類型</div>
                  <div class="detail">
                    <div>{{ compareData.selectedData.media.isPrime ? '- 欲投放的媒體 -' : '- 欲排除的媒體 -'}}</div>
                    <div>{{ showMedia(compareData.selectedData.media)}}</div>
                  </div>
                </div>
              </div>
              <!-- 第三列 -->
              <div class="compare-table-row">
                <div class="compare-table-column">
                  <div class="head">地區選擇 </div>
                  <div class="detail">{{ showCities(compareData.selectedData.cities) }}</div>
                </div>
                <div class="compare-table-column" v-if="userType === 'User'">
                  <div class="head">電信商選擇</div>
                  <div class="detail">{{ showOptionText('isps', compareData.selectedData.isps) }}</div>
                </div>
                <div class="compare-table-column" v-if="userType === 'User'">
                  <div class="head">手機品牌選擇</div>
