<script>
import { mapState, mapGetters, mapActions } from 'vuex'
import HighChart from '@/components/HighChart'
import compareChartConfig from '@/config/charts/compareChartConfig'

export default {
  name: 'Compare',
  components: {
    HighChart
  },
  data () {
    return {
      compareChartConfig,
      openDetail: [],
      chartOptions: {},
      compareChartData: {},
      chartData: {}
    }
  },
  computed: {
    ...mapState('calculation', ['options', 'compareDataList']),
    ...mapGetters('root', ['userType'])
  },
  watch: {
    compareDataList: {
      handler () {
        if (!this.$lodash.isEmpty(this.compareDataList)) {
          this.setChartData()
        }
      },
      deep: true,
      immediate: true
    }
  },
  mounted () {
    this.setChartData()
  },
  methods: {
    ...mapActions('calculation', ['removeCompareData', 'toggleCompareChartDisplay']),
    // 轉數字
    toLocaleString (number) {
      return number ? number.toLocaleString() : 0
    },
    // 轉百分比
    toPercentage (number) {
      return number ? `${Math.round(number * 100)}%` : `0%`
    },
    // 切換展開/隱藏細項
    toggleDetail (index) {
      this.$set(this.openDetail, index, !this.openDetail[index])
    },
    // 設定圖表資料
    setChartData () {
      const chartData = {
        nameList: this.compareDataList.filter(data => data.display).map(data => data.resultName),
        dataList: {
          mobileTotalUU: [],
          mobileReachUU: [],
          desktopTotalUU: [],
          desktopReachUU: []
        }
      }
      this.compareDataList
        .filter(data => data.display)
        .forEach(data => {
          chartData.dataList.mobileReachUU.push(data.calculationResult.mobile.reach_uu || 0)
          chartData.dataList.mobileTotalUU.push(data.calculationResult.mobile.total_uu || 0)
          chartData.dataList.desktopReachUU.push(data.calculationResult.desktop.reach_uu || 0)
          chartData.dataList.desktopTotalUU.push(data.calculationResult.desktop.total_uu || 0)
        })
      this.chartData = chartData
    },
    // 移除比較資料
    removeData (index) {
      this.$confirm('確定要刪除此筆資料?', {
        cancelButtonText: '取消',
        confirmButtonText: '確定'
      }).then(() => {
        this.removeCompareData(index)
        this.setChartData()
      }).catch(() => {})
    },
    // 一般選項(不需額外處理)ID轉Name
    showOptionText (type, ids) {
      const options = this.options[type]
      if (!this.$lodash.isEmpty(options)) {
        return options
          .filter(option => ids.includes(option.id))
          .map(data => data.name)
          .join(', ')
      }
    },
    // 年齡資料ID轉Name
    showDemographic (ids) {
      const allDemoOption = [...this.options.demographics.male, ...this.options.demographics.female]
      return allDemoOption
        .filter(option => ids.includes(option.id))
        .sort((a, b) => a.abbrev.localeCompare(b.abbrev))
        .map(data => data.name_cn.replace('歲', data.abbrev[0] === 'm' ? '男' : '女'))
        .join(', ')
    },
    // 興趣ID轉Name
    showInterest (selectedData) {
      if (selectedData.type.match(/interest|all/)) {
        const interestIds = selectedData.interestIds
        return this.options.interests
          .filter(interest => interestIds.includes(interest.id))
          .map(data => data.name).join(', ')
      } else {
        return '無'
      }
    },
    // 特定議題ID轉Name
    showKeyword (selectedData) {
      if (selectedData.type.match(/keyword|all/)) {
        const keywordIds = selectedData.keywordIds
        const currentKeywords = this.options.keywords.filter(option => keywordIds.includes(option.remote_id))
        const groupList = this.options.interests.map(interest => {
          const keywordList = this.options.keywords.filter(option => option.interest_id === interest.id)
          const hasAllKeyword = keywordList.every(data => keywordIds.includes(data.remote_id))
          return {
            interestName: interest.name,
            keywords: hasAllKeyword
              ? '全部'
              : currentKeywords
                .filter(keyword => keyword.interest_id === interest.id)
                .map(data => data.name).join(', ')
          }
        })
        return groupList.filter(group => !this.$lodash.isEmpty(group.keywords))
          .map(data => `${data.interestName}(${data.keywords})`)
          .join(', ')
      } else {
        return '無'
      }
    },
    // 媒體ID轉Name
    showMedia (mediaData) {
      const mediaIds = mediaData.isPrime ? mediaData.includeIds : mediaData.excludeIds
      const currentMedia = this.options.medias.categories.map(option => {
        const hasAllMedia = option.media.every(data => mediaIds.includes(data.uid))
        return {
          category: option.name,
          medias: hasAllMedia
            ? '全部'
            : option.media
              .filter(media => mediaIds.includes(media.uid))
              .map(data => data.description).join(', ')
        }
      })
      return currentMedia.filter(media => !this.$lodash.isEmpty(media.medias))
        .map(data => `${data.category}(${data.medias})`).join(', ')
    },
    // 城市ID轉Name
    showCities (cities) {
      const offshoreIslandIds = 5
      const municipalityIds = cities.municipalities
      const regionIds = cities.regions
      const regionNames = this.options.cities.regions
        .filter(region => regionIds.includes(region.id) && region.id !== offshoreIslandIds)
        .map(data => data.name).join(', ')
      const municipalityNames = this.options.cities.municipalities
        .filter(municipality => municipalityIds.includes(municipality.id))
        .map(data => data.city_tw).join(', ')
      return regionNames ? `${regionNames}, ${municipalityNames}` : municipalityNames
    }
  }
}
</script>

